@import "../_fresh-variables.less";
@import "../_fresh-mixins.less";

.fn-@{cms-ns}-recommend{
	background: #fff;

	.module-hd{
		padding:10px;
		overflow: hidden;

		.icon{
			display: block;
			.size(20px);
			float: left;
			background: data-uri('@{cms-image-dir}/@{fresh-ns}/fresh_recommon_icon.png');
			background-size: 100% 100%;
			margin-right:5px;
		}
		span{
			color:@fresh-color;
			float: left;
		}
		.right-icon{
			float: right;
			line-height: 20px;
			color:#bbb;
		}

	}
	.item{
		display: block;
		overflow: hidden;
		border-top:1px @border-color-base solid;
		padding:5px 10px;
		position: relative;
		.pic{
			float: right;
			width:130px;
		}
		.info{
			margin-right:35%;
			.mark{
				.size(38px);
				background-size: 100% 100%;
				text-align: center;
				font-size: 13px;
				line-height: 1.4;
				color:#fff;
				background-image: data-uri('@{cms-image-dir}/@{fresh-ns}/fresh_recommon_mark.png')
			}
			.tlt{
				margin-top:10px;
				.ellipsis();
			}
			.sub{
				.ellipsis();
			}
			.price{
				.absolute(_,_,0,10px);
				margin:6px 0;
				display: flex;
				align-items: center;
				align-items: flex-end;
				.recom-price{
					font-size:1.12rem;
					line-height: 1;

					.point, .rmb{
						font-size: .8rem;
					}
				}
				.discount{
					display: flex;
					flex-direction: column;
					justify-content: center;
					text-align: right;
					margin-left:-7px;

					> *{
						font-size:@font-size-sm;
					}

					em{
						background: #ff6537;
						color:#fff;
						font-style: normal;
						padding:0 3px;
						border-radius: 5px;
						position: relative;
						top: -6px;
					}
					.down{
						padding-left:10px;
					}
				}

			}
		}
	}

	.item-toggle{
		display: none;
	}

	.switch-item-btn{
		.down{
			display: table;
		}

		.up{
			display: none;
		}
	}

	.module-body.active{
		.item-toggle{
			display: block;
		}

		.switch-item-btn{
			.down{
				display: none;
			}

			.up{
				display: table;
			}
		}
	}
}

.switch-item-btn{
	padding: 20px 0;
	border-top:1px solid @border-color-base;

	.icon{
		.size(42px, 100%);
		position: relative;
		display:table-cell;
		transition: transform .3s ease;

		&:before,&:after{
			content: '';
			.absolute(50%,_,_,_);
			background: @fresh-color;
			.size(30%, 5%);
			transform-origin: 50% 50%;
		}

		&:before{
			left: 25%;
			transform: rotate(45deg);
		}

		&:after{
			right: 24.5%;
			transform: rotate(-45deg);
		}
	}

	.down{
		display:table;
		padding: 5px 5px 5px 20px;
		margin:0 auto;
		border-radius:50px;
		border: 1px solid @fresh-color;
		color: @fresh-color;
	}

	.up{
		display:table;
		padding: 5px 40px;
		margin:0 auto;
		border-radius:50px;
		border: 1px solid @fresh-color;
		color: @fresh-color;

		.icon{
			transform: rotate(180deg);
		}
	}
}
