@import "../_fresh-variables.less";
@import "../_fresh-mixins.less";

.fn-@{cms-ns}-img-group{
	.layout{
		position: relative;

		&:before{
		    display: block;
		    content: '';
		    padding-top: 25.8%;
		}

		.layout-wrap{
			position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    height: 100%;
		    z-index: 1;
		}

		.img-box{
			display:block;
			width:49.5%;
			float:left;
			margin:0 1% 1% 0;
			position:relative;

			&:nth-child(2), &:nth-child(4){
				margin-right:0;
			}

			&:before{
				display: block;
		    	content: '';
		    	padding-top: 50%;
			}

			.img{
				position: absolute;
			    top: 0;
			    left: 0;
			    width: 100%;
			    height: 100%;
			    max-height: 100%;
			}
		}
	}
}
