@import "../_fresh-variables.less";
@import "../_fresh-mixins.less";

@import "../../_plugin/cmp-level-flex.less";

.fn-@{cms-ns}-cookery-book{

	background: @body-background;

	.module-banner{
		position: relative;
		display: table;
		width:100%;
		img{
			width:100%;
			vertical-align: top;
		}
	
		&:after{
			display: block;
			content:'';
			.absolute(_,_,-2px,50%);
			width: 0;
		    height: 0;
		    border: 10.61px solid transparent;
		    border-bottom-color: #fff;
		    margin-left: -10.61px;
		    margin-top: -10.61px;
		}
	}

	.item-list{
		
		.cmp-level-list();

		.item{
			margin: 0 10px;
		}
	}
}
