@import "../_fresh-variables.less";
@import "../_fresh-mixins.less";

.fn-@{cms-ns}-pic-group{

	.layout{
		position: relative;
		margin-top:2px;

		&:first-child{
			margin-top: 0;
		}

		&:before{
			display: block;
			content:'';
			padding-top:28%;
		}

		&-wrap{
			.absolute(0,_,_,0);
			.size(100%);
			z-index:1;
		}

		&.col1{
			.img-box{
				float: left;
				width:100%;

				&:before{
					padding-top:28%;
				}
			}
		}

	}
}
